.Pagination {
  position: fixed;
  display: flex;
  flex-direction: column;
  z-index: 5;
  right: 25px;
  top: 50vh;
  transform: translateY(-50%);
}
.Pagination__dot {
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background-color: white;
  transition: background-color 0.3s linear;
}
.Pagination__dot:not(:first-child) {
  margin-top: 10px;
}
.Pagination__dot-active {
  background-color: #f78b1f;
}
